import { BookInfo } from './../bookInfo';
import { MYBOOKS } from '../../assets/data/myBooks';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-lefebvka',
  templateUrl: './lefebvka.component.html',
  styleUrls: ['./lefebvka.component.css']
})
export class LefebvkaComponent implements OnInit {
  mybooks = MYBOOKS;
  bookPicked: BookInfo;

  constructor() {}

  ngOnInit() {
    this.onButtonClick(0);
  }

  onButtonClick(index) {
    this.bookPicked = {
      bookName: this.mybooks[index].bookName,
      author: this.mybooks[index].author,
      genre: this.mybooks[index].genre,
      yearPublished: this.mybooks[index].yearPublished,
      picture: this.mybooks[index].picture
    };

    return this.bookPicked;
  }
}
